import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

export default function App() {
	const [results, setResults] = useState([]);
	const [query, setQuery] = useState('react hooks');
	const [loading, setLoading] = useState(false);
	const [error, setError] = useState(null);
	const searchInputRef = useRef();
	const searchBtn = useRef();

	useEffect(() => {
		getResults();
	}, []);

	const getResults = async () => {
		setLoading(true);
		searchInputRef.current.style = 'background: yellow';
		searchBtn.current.style = 'background:blue;color: white';

		try {
			// const response = await axios.get(`http://hn.algolia.com/api/v1/search?query=${query}`);
			const response = await axios.get(`https:/wpjs.co.uk/wpb/wp-json/wp/v2/posts`);
			setResults(response.data);
			console.log(response.data);
		} catch (err) {
			setError(err);
		}

		setLoading(false);
		searchInputRef.current.style = 'background:linen';
	};

	const handleSearch = (event) => {
		event.preventDefault();
		console.log('Search clicked');
		getResults();
		searchBtn.current.disabled = true;
		searchBtn.current.style = 'background:gray;color: white';
		searchBtn.current.style = 'cursor:not-allowed';
	};

	const handleClearSearch = () => {
		setQuery('');
		searchInputRef.current.focus();
	};

	return (
		<div className='container max-w-md mx-auto p-4 m-2 bg-purple-lightest shadow-lg rounded'>
			<img src='' alt='React Logo' className='float-right h-12' />
			<h1 className='text-black text-2xl '>wpjs.co.uk/wpb</h1>
			<form onSubmit={handleSearch} className='mb-2'>
				<input
					type='text'
					onChange={(event) => setQuery(event.target.value)}
					value={query}
					ref={searchInputRef}
					className='border p-1 rounded'
				/>
				<button type='submit' className='bg-orange rounded m-1 p-1' ref={searchBtn}>
					Search
				</button>
				<button type='button' onClick={handleClearSearch} className='bg-teal text-white p-1 rounded'>
					Clear
				</button>
			</form>

			{loading ? (
				<div className='font-bold text-orange-dark'>Loading results...</div>
			) : (
				<ul className='list-reset leading-normal'>
					{results.map((result) => (
						<li key={result.id}>
							<a href={result.link} className='text-indigo-dark hover:text-indigo-darkest'>
								{result.title.rendered}
							</a>
						</li>
					))}
				</ul>
			)}

			{error && <div className='text-red font-bold'>{error.message}</div>}
		</div>
	);
}
